import { g as gameHistoryTable } from "../../../../chunks/schema.js";
import { e as error, r as redirect } from "../../../../chunks/index.js";
const actions = {
  startGame: async ({ locals: { db, user }, request }) => {
    if (!user) return;
    const form = await request.formData();
    const href = form.get("href");
    const ghc = await db.insert(gameHistoryTable).values({
      gameId: 4,
      playerOneId: user?.id,
      isComputerOpponent: true
    }).returning().get().catch(() => {
      error(500, "Something went wrong");
    });
    redirect(303, `${href}?gh=${ghc.id}`);
  }
};
export {
  actions
};
