import { z } from "zod";
const playWithComputerSchema = z.object({
  variant: z.string().min(1),
  minutes: z.number().int().min(1),
  stakingAmount: z.number().int().min(1)
});
const scheduleGameSchema = z.object({
  opponentUserName: z.string().min(1)
});
const createTournamentSchema = z.object({
  timeControl: z.string().min(1)
});
const instantPlaySchema = z.object({
  arenaName: z.string().min(1)
});
const playWithFriendSchema = z.object({
  friendUsername: z.string().min(3, "Username must be at least 3 characters long").max(30, "Username must not exceed 30 characters"),
  variant: z.enum(["standard", "blitz", "rapid"], {
    required_error: "Please select a game variant"
  }),
  timeControl: z.enum(["5+0", "10+0", "15+10"], {
    required_error: "Please select a time control"
  }),
  stakingAmount: z.string().min(1, "Staking amount is required").refine((val) => !isNaN(Number(val.replace(/,/g, ""))), "Please enter a valid number").transform((val) => Number(val.replace(/,/g, ""))).refine((val) => val >= 100, "Minimum staking amount is ₦100").refine((val) => val <= 1e6, "Maximum staking amount is ₦1,000,000")
});
export {
  playWithFriendSchema as a,
  createTournamentSchema as c,
  instantPlaySchema as i,
  playWithComputerSchema as p,
  scheduleGameSchema as s
};
