import { u as usersTable } from "../../../../../../chunks/schema.js";
import { r as redirect } from "../../../../../../chunks/index.js";
import { eq } from "drizzle-orm";
const load = async ({ locals: { user } }) => {
  console.log("🚀 ~ load ~ user:", user);
  if (!user) {
    redirect(308, "/login");
  }
  return {
    user
  };
};
const actions = {
  default: async ({ locals: { user, db: db2 }, request }) => {
    if (!user) redirect(308, "/login");
    const form = await request.formData();
    const platform = form.get("platform");
    const handle = form.get("handle");
    switch (platform) {
      case "twitter":
        await db2.update(usersTable).set({
          xHandle: handle
        }).where(eq(usersTable.id, user.id));
        console.log("🚀 ~ default: ~ platform:", platform);
        return { handle };
      case "facebook":
        await db2.update(usersTable).set({
          facebookHandle: handle
        }).where(eq(usersTable.id, user.id));
        break;
      case "instagram":
        await db2.update(usersTable).set({
          instagramHandle: handle
        }).where(eq(usersTable.id, user.id));
        break;
    }
  }
};
export {
  actions,
  load
};
